import React, { useState, useEffect } from "react";
import AddIcon from "@material-ui/icons/Add";
import MUIDataTable from "mui-datatables";
import EditIcon from "@material-ui/icons/Edit";
import UploadIcon from '@material-ui/icons/CloudUpload';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";
import axios from "axios";
import { Link } from "react-router-dom";
import Upload from "../../components/Controls/Upload";
import {excelFormatDownload} from "../../components/Controls/ExcelFormatDownload";
import ExcelUpload from "../../components/Controls/ExcelUpload";
import IconButton from "@material-ui/core/IconButton";
import Popup from "../../components/Controls/Popup";
import Notification from "../../components/SnackBar/Notification";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles, Tooltip } from "@material-ui/core";
import SupplierForm from "./SupplierForm";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    marginLeft: theme.spacing(2),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(3),
  },
  iconButtonColor: {
    color: "#fff",
    "&:hover": {
      color: "#0000ff",
    },
  },
}));

export default function Suppliers() {
  const classes = useStyles();
  const [supplierList, setSupplierList] = useState([]);
  const [recordForEdit, setRecordForEdit] = useState(null);
  const [supplierRecord, setSupplierRecord] = useState(null);
  const [openPopup, setOpenPopup] = useState(false);
  const [upload, setUpload] = useState(false);

  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableToolbar: {
          root: {
            backgroundColor: "#50d07d",
            color: "#fff",
          },
          icon: {
            color: "#fff",
            "&:hover": {
              color: "#0000ff",
            },
          },
        },
        MuiTableCell: {
          root: {  //This can be referred from Material UI API documentation. 
              padding: '6px',
          },
        },
      },
    typography: {
      "fontFamily": `"Roboto", "Helvetica", "Arial", sans-serif`,
      "fontSize": 14,
      "fontWeightLight": 300,
      "fontWeightRegular": 400,
      "fontWeightMedium": 500
     }
    });

  async function fetchSuppliers() {
    const requestOptions = {
      headers: {
        method: "GET",
        headers: { "Content-Type": "application/json" },
      },
    };

    try {
      await axios
        .get("/api/supplier/list", { requestOptions })
        .then((res) => {
          setSupplierList(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (error) {
      console.log(error);
    }
  }

  const postSupplier = async (values, setSubmitting) => {
    const requestOptions = {
      headers: {
        method: "POST",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
      },
    };

    try {
      await axios
        .post("/api/supplier/create/", values, {
          requestOptions,
        })
        .then((resp) => {
          setSupplierRecord(resp.data);
          setSubmitting(false);
        });
    } catch (error) {
      console.log(error);
    }
  };

  const updateSupplier = async (values, setSubmitting) => {
    const requestOptions = {
      headers: {
        method: "PUT",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
      },
    };

    try {
      await axios
        .put(`/api/supplier/update/${values.id}/`, values, {
          requestOptions,
        })
        .then((resp) => {
          setSupplierRecord(resp.data);
          setSubmitting(false);
        });
    } catch (error) {
      console.log(error);
    }
  };

  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    fetchSuppliers();
  }, [supplierRecord]);

  const openInPopup = (item) => {
    setRecordForEdit(item);
    setOpenPopup(true);
  };

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "supplier_type_name",
      label: "Supplier Type",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "contact_person_name",
      label: "Contact Person",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "contact_person_number_one",
      label: "Contact Number No.1",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "Actions",
      options: {
        filter: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          let item;
          tableMeta.tableData.forEach(function (supplier) {
            if (supplier.id == tableMeta.rowData[0]) return (item = supplier);
          });
          return (
            <>
              <IconButton
                color="primary"
                onClick={() => {
                  openInPopup(item);
                }}
              >
                <EditIcon fontSize="default" />
              </IconButton>
              <Link
                to={{
                  pathname: "/app/supplier/details",
                  state: item,
                }}
              >
                <IconButton color="primary">
                  <ArrowRightAltIcon fontSize="default" />
                </IconButton>
              </Link>
            </>
          );
        },
      },
    },
  ];

  const addOrEdit = (supplier, resetForm, setSubmitting) => {
    if (supplier.id === 0) postSupplier(supplier, setSubmitting);
    else updateSupplier(supplier, setSubmitting);
    resetForm();
    setRecordForEdit(null);
    setOpenPopup(false);
    setNotify({
      isOpen: true,
      message: "Submitted Successfully",
      type: "success",
    });
  };

  const options = {
    filterType: "dropdown",
    selectableRows: "none",
    responsive: "standard",
    customToolbar: () => {
      return (
        <>
        <Tooltip title={"Download Excel Formate"}>
          <IconButton className={classes.iconButtonColor}
            onClick={() => {
              // setDownload(true);
              excelFormatDownload("/api/supplier/supplier_excel_format/", "Supplier Excel Format")
            }}
          >
            <CloudDownloadIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Upload Excel Sheet"}>
          <IconButton className={classes.iconButtonColor}
            onClick={() => {
              setUpload(true);
            }}
          >
            <UploadIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Add New"}>
          <IconButton className={classes.iconButtonColor}
            onClick={() => {
              setOpenPopup(true);
              setRecordForEdit(null);
            }}
          >
            <AddIcon />
          </IconButton>
        </Tooltip>
        </>
      );
    },
    print: false,
    // downloadOptions: { filename: "suppliers.csv" },
    download: false,
    pagination: true,
    rowsPerPage: 5,
    rowsPerPageOptions: [5, 10, 20, 30],
    serverSide: false, // Set to true if you are using server-side pagination
    count: supplierList.length, // Pass the total number of rows to display correct pagination count
  };

  return (
    <MuiThemeProvider theme={getMuiTheme()}>
      <div>
        <MUIDataTable
          title={"Supplier List"}
          data={supplierList}
          columns={columns}
          options={options}
          className={classes.pageContent}
        />
        <Popup
          title="Supplier Form"
          openPopup={openPopup}
          setOpenPopup={setOpenPopup}
        >
          <SupplierForm
            recordForEdit={recordForEdit}
            addOrEdit={addOrEdit}
            supplierList={supplierList}
          />
        </Popup>
        <Upload
          title="Upload File"
          upload={upload}
          setUpload={setUpload}
        >
          <ExcelUpload setChangeRecord={setSupplierRecord} uploadApiLink={"/api/supplier/create_from_excel/"} />
        </Upload>
        <Notification notify={notify} setNotify={setNotify} />
      </div>
    </MuiThemeProvider>
  );
}
