import React from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import classnames from "classnames";


// styles
import useStyles from "./styles";

// components
import Header from "../Header";
import Footer from "../Footer/Footer";
import Sidebar from "../Sidebar";

// pages
import Dashboard from "../../pages/dashboard";
import Fabrics from "../../pages/fabrics/Fabrics";
import FabricTypes from "../../pages/fabrics/FabricTypes";
import FabricDropdown from "../../pages/fabrics/FabricDropdown";
import FabricDetail from "../../pages/fabrics/FabricDetail";
import FabricQRcodePrint from "../../pages/fabrics/FabricQRcodePrint"
import SupplierDetail from "../../pages/suppliers/SupplierDetail";
import Styles from "../../pages/styles/Styles_1";
import StyleDetail from "../../pages/styles/StyleDetail_1"
import FabricIssueDetails from "../../pages/fabrics/FabricIssueDetails"
import StyleFormAndFabricDetail from "../../pages/styles/StyleForm_1"
import FabricIssue from "../../pages/fabrics/FabricIssue";
import FabricIssueForm from "../../pages/fabrics/FabricIssueForm"
import StyleDropdown from "../../pages/styles/StyleDropdown"
import Typography from "../../pages/typography";
import Notifications from "../../pages/notifications";
import Maps from "../../pages/maps";
import Tables from "../../pages/tables";
import Icons from "../../pages/icons";
import Charts from "../../pages/charts";
import FabricReport from "../../pages/reports/FabricReport";
import StyleReport from "../../pages/reports/StyleReport";
import Profile from "../../pages/profile/Profile";

// context
import { useLayoutState } from "../../context/LayoutContext";

function Layout(props) {
  var classes = useStyles();

  // global
  var layoutState = useLayoutState();

  return (
    <div className={classes.root}>
      <>
        <Header history={props.history} />
        <Sidebar />
        <div
          className={classnames(classes.content, {
            [classes.contentShift]: layoutState.isSidebarOpened,
          })}
        >
          <div className={classes.fakeToolbar} />
          <div>
          <Switch>
            <Route path="/app/dashboard" component={Dashboard} />
            <Route path="/app/fabrics" component={Fabrics} />
            <Route path="/app/fabric-dropdown" component={FabricDropdown} />
            <Route path="/app/fabric/details" component={FabricDetail} />
            <Route path="/app/supplier/details" component={SupplierDetail} />
            <Route path="/app/styles" component={Styles} />
            <Route path="/app/style/details" component={StyleDetail} />
            <Route path="/app/fabric/issue/details" component={FabricIssueDetails} />
            <Route path="/app/style/form" component={StyleFormAndFabricDetail}/>
            <Route path="/app/fabric/issue" component={FabricIssue} />
            <Route path="/app/fabric/issue/form" component={FabricIssueForm} />
            <Route path="/app/style-dropdown" component={StyleDropdown}/>
            <Route path="/app/fabric/report" component={FabricReport}/>
            <Route path="/app/style/report" component={StyleReport}/>
            <Route path="/app/typography" component={Typography} />
            <Route path="/app/tables" component={Tables} />
            <Route path="/app/notifications" component={Notifications} />
            <Route path="/app/profile" component={Profile} />
            <Route path="/app/fabric/qr/code" component={FabricQRcodePrint} />
            <Route
              exact
              path="/app/ui"
              render={() => <Redirect to="/app/ui/icons" />}
            />
            <Route path="/app/ui/maps" component={Maps} />
            <Route path="/app/ui/icons" component={Icons} />
            <Route path="/app/ui/charts" component={Charts} />
          </Switch>
          <Footer />
          </div>
        </div>
      </>
    </div>
  );
}

export default withRouter(Layout);
