import React, { useEffect, useState } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import * as yup from "yup";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';


import {
    Button,
    Paper,
    Grid,
    Table,
    TableBody,
    TableRow,
    TableCell,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    FormHelperText,
    CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var initialValues = {
  id: 0,
  fabric: "",
  issue_to: "",
  customer: "",
  quantity: "",
  unit_of_measure: "",
  purpose: ""
};


const FabricIssueForm = (props) => {

    const [displayFabricTable , setDisplayFabricTable] = useState(false)
    const [availability, setAvailability] = useState(0)
    const [fabricList, setFabricList] = useState([]);
    const [disableFabric, setDisableFabric] =  useState(false)
    const [unitOfMeasure, setUnitOfMeasure] = useState([]);
    const [disableYards, setDisableYards] = useState(true)
    const [singleFabric, setSingleFabric] = useState([]);
    const { addOrEdit, recordForEdit } = props;

    var full_name = localStorage.getItem('first_name') + " " + localStorage.getItem('last_name')

    const getFabricDetail = async (values) => {
        setDisplayFabricTable(true)
        const requestOptions = {
          headers: {
            method: "GET",
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Content-Type": "application/json",
            },
          },
        };
    
        try {
          await axios
            .get(`/api/fabric/get/${values}`, {
              requestOptions,
            })
            .then((resp) => {
                setSingleFabric(resp.data);
                resp.data.last_availability === null ? setAvailability(resp.data.initial_availability) : setAvailability(resp.data.last_availability)
            });
        } catch (error) {
          console.log(error);
        }
      };

    function fabricDetailShow(fabricId){
        fabricList.map((value) =>{
            if(value.id === fabricId){
                setSingleFabric(value)
                value.last_availability === null ? setAvailability(value.initial_availability) : setAvailability(value.last_availability)
                setDisplayFabricTable(true)
                if (recordForEdit){
                    setDisableYards(true)
                    setDisableFabric(true)
                }else{
                    setDisableYards(false)
                }
            }else if(fabricId === undefined){
                setDisplayFabricTable(false)
                if (recordForEdit){
                    setDisableYards(true)
                    setDisableFabric(true)
                }else{
                    setDisableYards(true)
                }
                setAvailability(0)
                setSingleFabric([])
            }
        })
        return true
    }

  const validationSchema = yup.object().shape({

    fabric: yup.number().required("Fabric is required")
    .test((values) => {
        return fabricDetailShow(values)
    }),
    quantity: yup
    .number()
    .typeError("Invalid Input: numbers please")
    .positive("Must be greater than zero")
    .required("Fabric Issue is required")
    .integer()
    .test(`is-${availability}`, 'Yards must be less than available stock',
        (values)=>{
            if(recordForEdit){
                return true
            }else{
                if(values<=availability){return true}
            }
        }
    ),
    unit_of_measure: yup.string().required("UOM is required"),
    issue_to: yup.string().required("Issue to is required"),
    customer: yup.string().required("Customer is required"),
  });

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      let data = new FormData();

        data.append('id', values.id);
        data.append('fabric', values.fabric);
        data.append('issue_by', full_name)
        data.append('issue_to', values.issue_to);
        data.append('customer', values.customer);
        data.append('quantity', values.quantity);
        data.append('unit_of_measure', values.unit_of_measure);
        data.append('purpose', values.purpose);
      addOrEdit(data, resetForm, setSubmitting);
    },
  });

  useEffect(() => {
        async function getFabricList() {
            const response = await fetch("/api/fabric/list");
            const body = await response.json();
            setFabricList(body);
        }
        getFabricList();
        async function getUnitOfMeasure() {
            const response = await fetch("/api/unit_of_measure/list");
            const body = await response.json();
            setUnitOfMeasure(body);
        }
        getUnitOfMeasure();
    }, []);
  useEffect(() => {
    if (recordForEdit != null){
        setDisableFabric(true)
        setDisplayFabricTable(true)
        getFabricDetail(recordForEdit.fabric)
      formik.setValues({
        ...recordForEdit,
      });
    }
        
  }, [recordForEdit]);


  return(
    <div>
        <Form onSubmit={formik.handleSubmit}>
            <Grid justify="space-between" container spacing={5} style={{ paddingLeft: 16, paddingRight: 16, paddingBottom: 24 }}>
                <Grid item md={6}>
                    <Paper elevation={3} square={false} style={{ padding: 24, paddingBottom: 24 }}>
                        <Grid container justify="space-between" alignItems="flex-start" spacing={2}>
                            <Grid item md={12} sm={12} xs={12}>
                                {
                                    recordForEdit ? "" : <Autocomplete
                                    id="fabric"
                                    options={fabricList}
                                    disabled = {disableFabric}
                                    hide = "true"
                                    renderInput={params => (
                                    <TextField {...params} label="Fabric" variant="outlined" />
                                    )}
                                    getOptionLabel={option => option.dekko_reference}
                                    // value={formik.values.fabric}
                                    // value={fabricList?.find((value)=> value.id === recordForEdit?.fabric.id) || ""}
                                    onChange={(_event, fabric) => {
                                    if(fabric){
                                        formik.setFieldValue("fabric", fabric.id)
                                    }
                                    }}
                                    // multiple='true'
                                />
                                }
                                
                                {/* <FormControl
                                    variant="outlined"
                                    fullWidth
                                    {...(formik.touched.fabric && {
                                    error: Boolean(formik.errors.fabric),
                                    })}
                                >
                                    <InputLabel>Fabric</InputLabel>
                                        <Select
                                            label="Fabric"
                                            name="fabric"
                                            disabled = {disableFabric}
                                            value={formik.values.fabric}
                                            onChange={formik.handleChange}
                                            onBlur={formik.handleBlur}
                                            >
                                            <MenuItem value="">None</MenuItem>
                                            {fabricList.map((item) => (
                                                <MenuItem key={item.id} value={item.id}>
                                                {item.dekko_reference}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    {formik.touched.fabric &&
                                    Boolean(formik.errors.fabric) && (
                                        <FormHelperText error={true}>
                                        Please select a fabric
                                        </FormHelperText>
                                    )}
                                </FormControl> */}
                            </Grid>

                            <Grid item md={6} sm={6} xs={12}>
                                <Controls.Input
                                    label="Issue To"
                                    name="issue_to"
                                    value={formik.values.issue_to}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.issue_to && Boolean(formik.errors.issue_to)}
                                    helperText={formik.touched.issue_to && formik.errors.issue_to}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={6} sm={6} xs={12}>
                                <Controls.Input
                                    label="Customer"
                                    name="customer"
                                    value={formik.values.customer}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.customer && Boolean(formik.errors.customer)}
                                    helperText={formik.touched.customer && formik.errors.customer}
                                    fullWidth
                                />
                            </Grid>

                            <Grid item md={6} sm={6} xs={12}>
                                <Controls.Input
                                    label="Quantity"
                                    name="quantity"
                                    disabled = {disableYards}
                                    value={formik.values.quantity}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.quantity && Boolean(formik.errors.quantity)}
                                    helperText={formik.touched.quantity && formik.errors.quantity}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={6} sm={6} xs={12}>
                                <Controls.Select
                                    label="UOM"
                                    name="unit_of_measure"
                                    disabled = {disableYards}
                                    value={formik.values.unit_of_measure}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    options={unitOfMeasure}
                                    error={formik.touched.unit_of_measure && Boolean(formik.errors.unit_of_measure)}
                                    helperText={formik.touched.unit_of_measure && formik.errors.unit_of_measure}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={12} sm={12} xs={12}>
                                <Controls.Input
                                    label="Purpose"
                                    name="purpose"
                                    value={formik.values.purpose}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    fullWidth
                                    multiline
                                    rows={3}
                                />
                            </Grid>
                            <Grid item style={{ marginTop: 3 }} md={6} sm={6} xs={6}>
                                <div className={classes.wrapper}>
                                    <Controls.Button
                                        type="submit"
                                        disabled={formik.isSubmitting}
                                        text="Submit"
                                    />                                  
                                    <Controls.Button
                                        text="Reset"
                                        color="default"
                                        onClick = {(e)=>{
                                            formik.resetForm(e)
                                            setDisplayFabricTable(false)
                                        }}
                                    />
                                </div> 
                            </Grid>
                        </Grid>                          
                    </Paper>
                </Grid>
                <Grid item md={6} style={{display: displayFabricTable ? 'block' : 'none' }}>
                    <Paper elevation={3} square={false} style={{ paddingTop: 6 }}>
                        <Grid container alignItems="flex-start" spacing={6}>
                            <Grid item md={6} sm={6} xs={12}>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell>Mill Reference:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.mill_reference}
                                                </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Dekko Reference:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.dekko_reference}
                                                </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Supplier Name:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.supplier_name}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Fabric Type:</TableCell>
                                            <TableCell align="right">
                                            {singleFabric.fabric_type_name}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Composition:</TableCell>
                                            <TableCell align="right">
                                            {singleFabric.fabric_composition}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                        <TableCell>Weave:</TableCell>
                                            <TableCell align="right">
                                            {singleFabric.weave_name}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Appearance:</TableCell>
                                            <TableCell align="right">
                                            {singleFabric.appearance_name}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Construction:</TableCell>
                                            <TableCell align="right">
                                            {singleFabric.fabric_construction}
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </Grid>
                            <Grid item md={6} sm={6} xs={12}>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell>Shrinkage:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.warp}, {singleFabric.weft}
                                            </TableCell>
                                        </TableRow>
                                        {/* <TableRow>
                                            <TableCell>weight:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.weight} {singleFabric.weight_unit_value}
                                            </TableCell>
                                        </TableRow> */}
                                        <TableRow>
                                            <TableCell>Cuttable Width:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.cuttable_width} {singleFabric.cuttable_width_unit_value}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Price:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.price_free_on_board}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>MOQ:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.moq} {singleFabric.moq_unit_value}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Lead Time:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.lead_time} {singleFabric.lead_time_unit_value}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Marketing Tools:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.marketing_tools}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Initial Availability:</TableCell>
                                            <TableCell align="right">
                                                {singleFabric.initial_availability} {singleFabric.unit_of_measure_value}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Current Availability:</TableCell>
                                            <TableCell align="right">
                                                {availability} {singleFabric.unit_of_measure_value}
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </Grid>
                        </Grid>
                    </Paper>
                </Grid>           
            </Grid>
        </Form>
    </div>
)
};

export default FabricIssueForm;
