import React, { useState, useEffect } from "react";
import { useForm, Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import Divider from "@material-ui/core/Divider";
import Typography from "@material-ui/core/Typography";
import Autocomplete from '@material-ui/lab/Autocomplete';
import Select from "@material-ui/core/Select";
import { useFormik } from "formik";
import * as yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  InputLabel,
  Icon,
  Grid,
  Radio,
  RadioGroup,
  FormControl,
  MenuItem,
  TextField,
  CircularProgress,
  FormHelperText,
} from "@material-ui/core";
import { VerticalAlignCenter } from "@material-ui/icons";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var today = new Date()
var fromDate = new Date()
fromDate.setMonth(fromDate.getMonth() - 1)

var initialFValues = {
  id: 0,
  from_date: fromDate,
  to_date: today,
  dekko_reference: "",
  mill_reference: "",
  supplier: "",
  fabric_type: "",
  composition: "",
  weave: "",
  appearance: "",
  buyer: "",
  composition: "",
  construction: "",
};

const validationSchema = yup.object().shape({
  mill_reference: yup.string().required("Mill Reference is required"),
  supplier: yup.string().required("Please select a supplier"),
  fabric_type: yup.string().required("Please select a fabric type"),
  composition: yup.string().required("Please select a composition"),
  weave: yup.string().required("Please select a weave"),
  appearance: yup.string().required("Please select a appearance")
});

const FabricReportForm = (props) => {
  const [suppliers, setSuppliers] = useState([]);
  const [millAndDekkoReferences, setMillAndDekkoReferences] = useState([]);
  const [fabricTypes, setFabricTypes] = useState([]);
  const [weaves, setWeaves] = useState([]);
  const [appearances, setAppearances] = useState([]);
  const [buyers, setBuyers] = useState([]);
  const [compositions, setCompositions] = useState([]);
  const [constructions, setConstructions] = useState([]);

  const { addOrEdit, recordForEdit, fabricList } = props;
  const { values, setValues, handleChange } = useForm(initialFValues);

  const classes = style();

  const formik = useFormik({
    initialValues: initialFValues,
    // validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      addOrEdit(values, resetForm, setSubmitting);
    },
  });


  useEffect(() => {
    if (recordForEdit != null)
      formik.setValues({
        ...recordForEdit,
      });
  }, [recordForEdit]);

  useEffect(() => {
    async function getSuppliers() {
      const response = await fetch("/api/supplier/list");
      const body = await response.json();
      setSuppliers(body);
    }
    getSuppliers();

    async function getMillAndDekkoReference() {
      const response = await fetch("/api/fabric_mill_and_dekko_reference/list");
      const body = await response.json();
      setMillAndDekkoReferences(body);
    }
    getMillAndDekkoReference();

    async function getFabricTypes() {
      const response = await fetch("/api/fabric_type/list");
      const body = await response.json();
      setFabricTypes(body);
    }
    getFabricTypes();

    async function getWeaves() {
      const response = await fetch("/api/weave/list");
      const body = await response.json();
      setWeaves(body);
    }
    getWeaves();

    async function getAppearances() {
      const response = await fetch("/api/appearance/list");
      const body = await response.json();
      setAppearances(body);
    }
    getAppearances();

    async function getBuyers() {
      const response = await fetch("/api/buyer/list");
      const body = await response.json();
      setBuyers(body);
    }
    getBuyers();

    async function getCompositions() {
      const response = await fetch("/api/composition/list");
      const body = await response.json();
      setCompositions(body);
    }
    getCompositions();

    async function getConstructions() {
      const response = await fetch("/api/construction/list");
      const body = await response.json();
      setConstructions(body);
    }
    getConstructions();
  }, []);

  const millReferenceList = {
    options: millAndDekkoReferences.map((option) => option.mill_reference),
  };

  const dekkoReferenceList = {
    options: millAndDekkoReferences.map((option) => option.dekko_reference),
  };

  return (
    <Form onSubmit={formik.handleSubmit}>
      <Grid container alignItems="flex-start" spacing={2}>
        <Grid item md={3} sm={4} xs={6}>
          <Controls.DatePicker
            label="From"
            name="from_date"
            value={formik.values.from_date}
            onChange={value => {
              formik.setFieldValue("from_date", value)
            }}
            onBlur={formik.handleBlur}
            fullWidth
          />
        </Grid>
        <Grid item md={3} sm={4} xs={6}>
        <Controls.DatePicker
            label="To"
            name="to_date"
            value={formik.values.to_date}
            onChange={value => {
              formik.setFieldValue("to_date", value)
            }}
            onBlur={formik.handleBlur}
            fullWidth
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Autocomplete fullWidth
              {...millReferenceList}
              id="auto-complete"
              name="mill_reference"
              onChange={(e, value) => {
                formik.setFieldValue(
                  "mill_reference",
                  value !== null ? value : initialFValues.mill_reference
                );
              }}
              value={formik.values.mill_reference}
              renderInput={params => (
                <TextField
                  margin="normal"
                  label="Mill Reference"
                  fullWidth
                  name="mill_reference"
                  {...params}
                />
              )}
            />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
            <Autocomplete fullWidth
                {...dekkoReferenceList}
                id="auto-complete"
                autoComplete
                includeInputInList
                width = "fit-content"
                onChange={(e, value) => {
                  formik.setFieldValue(
                    "dekko_reference",
                    value !== null ? value : initialFValues.dekko_reference
                  );
                }}
                value={formik.values.dekko_reference}
                renderInput={(params) => 
                    <TextField 
                    name="dekko_reference"
                    label="Dekko Reference"
                    margin="normal"
                    style={{width: "100%"}}
                    {...params}
                    />
                }
              />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Supplier"
            name="supplier"
            onChange={(e, value) => {
              formik.setFieldValue(
                "supplier",
                value !== null ? value : initialFValues.supplier
              );
            }}
            value={formik.values.supplier}
            onBlur={formik.handleBlur}
            options={suppliers}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Fabric Type"
            name="fabric_type"
            
            onChange={(e, value) => {
              formik.setFieldValue(
                "fabric_type",
                value !== null ? value : initialFValues.fabric_type
              );
            }}
            value={formik.values.fabric_type}
            onBlur={formik.handleBlur}
            options={fabricTypes}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Weave"
            name="weave"
            onChange={(e, value) => {
              formik.setFieldValue(
                "weave",
                value !== null ? value : initialFValues.weave
              );
            }}
            value={formik.values.weave}
            onBlur={formik.handleBlur}
            options={weaves}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Appearance"
            name="appearance"
            onChange={(e, value) => {
              formik.setFieldValue(
                "appearance",
                value !== null ? value : initialFValues.appearance
              );
            }}
            value={formik.values.appearance}
            onBlur={formik.handleBlur}
            options={appearances}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Buyer"
            name="buyer"
            onChange={(e, value) => {
              formik.setFieldValue(
                "buyer",
                value !== null ? value : initialFValues.buyer
              );
            }}
            value={formik.values.buyer}
            onBlur={formik.handleBlur}
            options={buyers}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Composition"
            name="composition"
            onChange={(e, value) => {
              formik.setFieldValue(
                "composition",
                value !== null ? value : initialFValues.composition
              );
            }}
            value={formik.values.composition}
            onBlur={formik.handleBlur}
            options={compositions}
          />
        </Grid>
        <Grid item md={2} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Construction"
            name="construction"
            onChange={(e, value) => {
              formik.setFieldValue(
                "construction",
                value !== null ? value : initialFValues.construction
              );
            }}
            value={formik.values.construction}
            onBlur={formik.handleBlur}
            options={constructions}
          />
        </Grid>
        
      </Grid>
      <Grid item style={{ marginTop: 16 }}>
          <div className={classes.wrapper}>
            <Controls.Button
              type="submit"
              disabled={formik.isSubmitting}
              text="Submit"
            />
            {formik.isSubmitting && (
              <CircularProgress size={24} className={classes.buttonProgress} />
            )}
            <Controls.Button
              text="Reset"
              color="default"
              onClick={formik.resetForm}
            />
          </div>
        </Grid>
    </Form>
  );
};

export default FabricReportForm;
