import React, { useState, useEffect } from "react";
import { useForm, Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import { makeStyles } from "@material-ui/core/styles";
import {
  Grid,
  CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var today = new Date()
var fromDate = new Date()
fromDate.setMonth(fromDate.getMonth() - 1)

var initialFValues = {
  id: 0,
  from_date: fromDate,
  to_date: today,
  name: "",
  buyer: "",
  designer: "",
  sample_type: "",
  wash_type: ""
};

const StyleReportForm = (props) => {
  const [styleName, setStyleName] = useState([]);
  const [designers, setDesigners] = useState([]);
  const [sampleTypes, setSampleTypes] = useState([]);
  const [WashTypes, setWashTypes] = useState([]);
  const [buyers, setBuyers] = useState([]);

  const { addOrEdit, recordForEdit, fabricList } = props;

  const classes = style();

  const formik = useFormik({
    initialValues: initialFValues,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      addOrEdit(values, resetForm, setSubmitting);
    },
  });


  useEffect(() => {
    if (recordForEdit != null)
      formik.setValues({
        ...recordForEdit,
      });
  }, [recordForEdit]);

  useEffect(() => {

    async function getStyleNameList() {
      const response = await fetch("/api/style/name/list");
      const body = await response.json();
      setStyleName(body);
    }
    getStyleNameList();

    async function getStyleBuyerList() {
      const response = await fetch("/api/style/buyer/list");
      const body = await response.json();
      setBuyers(body);
    }
    getStyleBuyerList();

    async function getDesigners() {
      const response = await fetch("/api/designer/list");
      const body = await response.json();
      setDesigners(body);
    }
    getDesigners();

    async function getSampleTypes() {
      const response = await fetch("/api/sample_type/list");
      const body = await response.json();
      setSampleTypes(body);
    }
    getSampleTypes();

    async function getWashTypes() {
      const response = await fetch("/api/wash_type/list");
      const body = await response.json();
      setWashTypes(body);
    }
    getWashTypes();
  }, []);


  const buyerList = buyers.map(item => {
    return { id: item.id, name: item.buyer };
  });

  return (
    <Form onSubmit={formik.handleSubmit}>
      <Grid container alignItems="flex-start" spacing={2}>
        <Grid item md={3} sm={4} xs={6}>
          <Controls.DatePicker
            label="From"
            name="from_date"
            value={formik.values.from_date}
            onChange={value => {
              formik.setFieldValue("from_date", value)
            }}
            onBlur={formik.handleBlur}
            fullWidth
          />
        </Grid>
        <Grid item md={3} sm={4} xs={6}>
        <Controls.DatePicker
            label="To"
            name="to_date"
            value={formik.values.to_date}
            onChange={value => {
              formik.setFieldValue("to_date", value)
            }}
            onBlur={formik.handleBlur}
            fullWidth
          />
        </Grid>
        <Grid item md={3} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Name"
            name="name"
            onChange={(e, value) => {
              formik.setFieldValue(
                "name",
                value !== null ? value : initialFValues.name
              );
            }}
            value={formik.values.name}
            onBlur={formik.handleBlur}
            options={styleName}
          />
        </Grid>
        <Grid item md={3} sm={4} xs={6}>
          <Controls.Autocomplete
            label="Buyer"
            name="buyer"
            onChange={(e, value) => {
              formik.setFieldValue(
                "buyer",
                value !== null ? value : initialFValues.buyer
              );
            }}
            value={formik.values.buyer}
            onBlur={formik.handleBlur}
            options={buyerList}
          />
        </Grid>
        <Grid item md={4} sm={12} xs={12}>
          <Controls.Autocomplete
            label="Designer"
            name="designer"
            onChange={(e, value) => {
              formik.setFieldValue(
                "designer",
                value !== null ? value : initialFValues.designer
              );
            }}
            value={formik.values.designer}
            onBlur={formik.handleBlur}
            options={designers}
          />
        </Grid>
        <Grid item md={4} sm={12} xs={12}>
          <Controls.Autocomplete
            label="Sample Type"
            name="sample_type"
            onChange={(e, value) => {
              formik.setFieldValue(
                "sample_type",
                value !== null ? value : initialFValues.sample_type
              );
            }}
            value={formik.values.sample_type}
            onBlur={formik.handleBlur}
            options={sampleTypes}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Controls.Autocomplete
            label="Wash Type"
            name="wash_type"
            
            onChange={(e, value) => {
              formik.setFieldValue(
                "wash_type",
                value !== null ? value : initialFValues.wash_type
              );
            }}
            value={formik.values.wash_type}
            onBlur={formik.handleBlur}
            options={WashTypes}
          />
        </Grid>
      </Grid>
      <Grid item style={{ marginTop: 16 }}>
          <div className={classes.wrapper}>
            <Controls.Button
              type="submit"
              disabled={formik.isSubmitting}
              text="Submit"
            />
            {formik.isSubmitting && (
              <CircularProgress size={24} className={classes.buttonProgress} />
            )}
            <Controls.Button
              text="Reset"
              color="default"
              onClick={formik.resetForm}
            />
          </div>
        </Grid>
    </Form>
  );
};

export default StyleReportForm;
