import React, { useState, useEffect } from "react";
import AddIcon from "@material-ui/icons/Add";
import MUIDataTable from "mui-datatables";
import EditIcon from "@material-ui/icons/Edit";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";
import BreadCrumb from "../../components/BreadCrumb/BreadCrumb";
import axios from "axios";
import { Link } from "react-router-dom";
import Upload from "../../components/Controls/Upload";
import IconButton from "@material-ui/core/IconButton";
import Popup from "../../components/Controls/Popup";
import Notification from "../../components/SnackBar/Notification";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles, Tooltip } from "@material-ui/core";
import FabricIssueForm from "./FabricIssueForm";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(2),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: "75%",
  },
  newButton: {
    position: "absolute",
    right: "10px",
  },
  iconButtonColor: {
    color: "#fff",
    '&:hover': {
      color: "#0000ff"
  }
  }
}));

export default function Suppliers() {
  const classes = useStyles();
  const [fabricIssueList, setFabricIssueList] = useState([]);
  const [recordForEdit, setRecordForEdit] = useState(null);
  const [fabricIssueRecord, setFabricIssueRecord] = useState(null);
  const [openPopup, setOpenPopup] = useState(false);

  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableToolbar: {
          root: {
            backgroundColor: "#50d07d",
            color: "#fff",
          },
          icon: {
            color: "#fff",
            "&:hover": {
              color: "#0000ff",
            },
          },
        },
        MuiTableCell: {
          root: {  //This can be referred from Material UI API documentation. 
              padding: '12px',
          },
        },
        MuiDialog:{
          paperWidthMd:{
            maxWidth: '1400px',
          }
         }
      },
    typography: {
      "fontFamily": `"Roboto", "Helvetica", "Arial", sans-serif`,
      "fontSize": 14,
      "fontWeightLight": 300,
      "fontWeightRegular": 400,
      "fontWeightMedium": 500
     }
    });

  async function fetchFabricIssue() {
    const requestOptions = {
      headers: {
        method: "GET",
        headers: { "Content-Type": "application/json" },
      },
    };

    try {
      await axios
        .get("/api/fabric/issue/list", { requestOptions })
        .then((res) => {
          setFabricIssueList(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (error) {
      console.log(error);
    }
  }

  const postFabricIssue = async (values, setSubmitting) => {
    const requestOptions = {
      headers: {
        method: "POST",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
      },
    };

    try {
      await axios
        .post("/api/fabric/issue/create/", values, {
          requestOptions,
        })
        .then((resp) => {
          setFabricIssueRecord(resp.data);
          setSubmitting(false);
        });
    } catch (error) {
      console.log(error);
    }
  };

  const updateFabricIssue = async (values, id, setSubmitting) => {
    const requestOptions = {
      headers: {
        method: "PUT",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
      },
    };

    try {
      await axios
        .put(`/api/fabric/issue/update/${id}/`, values, {
          requestOptions,
        })
        .then((resp) => {
          setFabricIssueRecord(resp.data);
          setSubmitting(false);
        });
    } catch (error) {
      console.log(error);
    }
  };

  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    fetchFabricIssue();
  }, [fabricIssueRecord]);

  const openInPopup = (item) => {
    setRecordForEdit(item);
    setOpenPopup(true);
  };

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "fabric_dekko_reference",
      label: "Fabric",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
        name: "issue_to",
        label: "Issue To",
        options: {
          filter: true,
          sort: false,
        },
      },
    {
      name: "customer",
      label: "Customer",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "fabric_issue",
      label: "Quantity",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "purpose",
      label: "Purpose",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "Actions",
      options: {
        filter: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          let item;
          tableMeta.tableData.forEach(function (supplier) {
            if (supplier.id == tableMeta.rowData[0]) return (item = supplier);
          });
          return (
            <>
              <IconButton
                color="primary"
                onClick={() => {
                  openInPopup(item);
                }}
              >
                <EditIcon fontSize="default" />
              </IconButton>
              <Link
                to={{
                  pathname: "/app/fabric/issue/details",
                  state: item,
                }}
              >
                <IconButton color="primary">
                  <ArrowRightAltIcon fontSize="default" />
                </IconButton>
              </Link>
            </>
          );
        },
      },
    },
  ];

  const addOrEdit = (fabricIssue, resetForm, setSubmitting) => {
    for (var key of fabricIssue.entries()) {
      console.log(key)
      if(key[0]=='id'){
          let id = key[1]
          if(key[1] == 0){
            postFabricIssue(fabricIssue, setSubmitting)
          }else{
            updateFabricIssue(fabricIssue, id, setSubmitting);
          }
      }
  }
    // if (supplier.id === 0) postSupplier(supplier, setSubmitting);
    // else updateSupplier(supplier, setSubmitting);
    resetForm();
    setRecordForEdit(null);
    setOpenPopup(false);
    setNotify({
      isOpen: true,
      message: "Submitted Successfully",
      type: "success",
    });
  };

  const options = {
    filterType: "dropdown",
    selectableRows: "none",
    responsive: "standard",
    customToolbar: () => {
      return (
        <>
        <Tooltip title={"Add New"}>
          <IconButton className={classes.iconButtonColor}
            onClick={() => {
              setOpenPopup(true);
              setRecordForEdit(null);
            }}
          >
            <AddIcon />
          </IconButton>
        </Tooltip>
        </>
      );
    },
    print: false,
    download: false
  };

  return (
    <MuiThemeProvider theme={getMuiTheme()}>
      <div>
      <BreadCrumb routeSegments={[{ name: "Fabric Issue" }]} />
        <MUIDataTable
          title={"Fabric Issue List"}
          data={fabricIssueList}
          columns={columns}
          options={options}
          className={classes.pageContent}
        />
        <Popup
          title="Fabric Issue Form"
          openPopup={openPopup}
          setOpenPopup={setOpenPopup}
        >
          <FabricIssueForm
            recordForEdit={recordForEdit}
            addOrEdit={addOrEdit}
            // fabricIssueList={fabricIssueList}
          />
        </Popup>
        <Notification notify={notify} setNotify={setNotify} />
      </div>
    </MuiThemeProvider>
  );
}
