import React, { useEffect, useState } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import * as yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {duplicateNameCheck} from '../fabrics/FabricTypeForm'
import {
  Grid,
  CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var initialValues = {
  id: 0,
  name: "",
  supplier_type: "",
  contact_person_name: "",
  contact_person_number_one: "",
  contact_person_number_two: "",
  contact_person_number_three: "",
  contact_person_email: "",
  license_number: "",
  vat_number: "",
  corporate_address: "",
  country: "",
  supplier_address: "",
};

let supplierList;
function holdSupplierList(SupplierList){
  supplierList = SupplierList
}

const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/



const SupplierForm = (props) => {
  const [supplierTypes, setSupplierTypes] = useState([]);
  const [countries, setCountries] = useState([]);

  const { addOrEdit, recordForEdit, supplierList } = props;
  holdSupplierList(supplierList)

  const validationSchema = yup.object().shape({
    name: yup.string()
      .required("Name is required")
      .test("Unique", "Name needs te be unique", (values) => {
        return duplicateNameCheck(supplierList, recordForEdit, values)
      }),
    vat_number: yup.number()
    .typeError("Invalid Input: numbers please")
    .positive("Must be greater than zero")
    .integer(),
    contact_person_email: yup.string().email("Invalid email format"),
    // corporate_address: yup.string().required("Corporate address is required"),
    // supplier_address: yup.string().required("Supplier address is required"),
    // contact_person_number_one: yup.string().matches(phoneRegExp, 'Phone number is not valid'),
  });

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      if(values.vat_number === ""){
        values.vat_number = 0;
      }
      addOrEdit(values, resetForm, setSubmitting);
    },
  });

  useEffect(() => {
    if (recordForEdit != null)
      formik.setValues({
        ...recordForEdit,
      });
  }, [recordForEdit]);

  useEffect(() => {
    async function getSupplierTypes() {
      const response = await fetch("/api/supplier_type/list");
      const body = await response.json();
      setSupplierTypes(body);
    }
    getSupplierTypes();
    async function getCountries() {
      const response = await fetch("/api/country/list");
      const body = await response.json();
      setCountries(body);
    }
    getCountries();
  }, []);


  return (
    <Form onSubmit={formik.handleSubmit}>
      <Grid container alignItems="flex-start" spacing={2}>
        <Grid item md={6} sm={12} xs={12}>
          <Controls.Input
            label="Name"
            name="name"
            id="name"
            value={formik.values.name}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.name && Boolean(formik.errors.name)}
            helperText={formik.touched.name && formik.errors.name}
            fullWidth
          />
        </Grid>
        <Grid item md={6} sm={12} xs={12}>
          <Controls.Select
            label="Supplier Type"
            name="supplier_type"
            value={formik.values.supplier_type}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            options={supplierTypes}
          />
        </Grid>
        <Grid item md={6} sm={12} xs={12}>
          <Controls.Input
            label="Contact Person Name"
            name="contact_person_name"
            value={formik.values.contact_person_name}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            fullWidth
          />
        </Grid>
        <Grid item md={6}>
          <Controls.Input
            label="Contact Person Email"
            name="contact_person_email"
            value={formik.values.contact_person_email}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.contact_person_email && Boolean(formik.errors.contact_person_email)}
            helperText={formik.touched.contact_person_email && formik.errors.contact_person_email}
            fullWidth
          />
        </Grid>
        <Grid item md={4}>
          <Controls.Input
            label="Contact Person No. 1"
            name="contact_person_number_one"
            value={formik.values.contact_person_number_one}
            onChange={formik.handleChange}
            fullWidth
          />
        </Grid>
        <Grid item md={4}>
          <Controls.Input
            label="Contact Person No. 2"
            name="contact_person_number_two"
            value={formik.values.contact_person_number_two}
            onChange={formik.handleChange}
            fullWidth
          />
        </Grid>
        <Grid item md={4}>
          <Controls.Input
            label="Contact Person No. 3"
            name="contact_person_number_three"
            value={formik.values.contact_person_number_three}
            onChange={formik.handleChange}
            fullWidth
          />
        </Grid>
        
        <Grid item md={4}>
          <Controls.Input
            label="License Number"
            name="license_number"
            value={formik.values.license_number}
            onChange={formik.handleChange}
            fullWidth
          />
        </Grid>
        <Grid item xs={4}>
          <Controls.Input
            label="Vat Number"
            name="vat_number"
            value={formik.values.vat_number}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.vat_number && Boolean(formik.errors.vat_number)}
            helperText={formik.touched.vat_number && formik.errors.vat_number}
            fullWidth
          />
        </Grid>
        <Grid item md={4} sm={6} xs={6}>
          <Controls.Select
            label="Country"
            name="country"
            value={formik.values.country}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            options={countries}
            error={
              formik.touched.country && Boolean(formik.errors.country)
            }
            helperText={formik.touched.country && formik.errors.country}
          />
        </Grid>
        <Grid item xs={6}>
          <Controls.Input
            label="Corporate Address"
            name="corporate_address"
            value={formik.values.corporate_address}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.corporate_address && Boolean(formik.errors.corporate_address)}
            helperText={formik.touched.corporate_address && formik.errors.corporate_address}
            fullWidth
          />
        </Grid>
        
        <Grid item xs={6}>
          <Controls.Input
            label="Supplier Address"
            name="supplier_address"
            value={formik.values.supplier_address}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.supplier_address && Boolean(formik.errors.supplier_address)}
            helperText={formik.touched.supplier_address && formik.errors.supplier_address}
            fullWidth
          />
        </Grid>
        <Grid item style={{ marginTop: 16 }}>
          <div className={classes.wrapper}>
            <Controls.Button
              type="submit"
              disabled={formik.isSubmitting}
              text="Submit"
            />
            {formik.isSubmitting && (
              <CircularProgress size={24} className={classes.buttonProgress} />
            )}
            <Controls.Button
              text="Reset"
              color="default"
              onClick={formik.resetForm}
            />
          </div>
        </Grid>
      </Grid>
    </Form>
  );
};

export default SupplierForm;
