import React, { useEffect, useState } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import * as yup from "yup";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
import Autocomplete from '@material-ui/lab/Autocomplete';
import axios from "axios";
import {duplicateNameCheck} from '../fabrics/FabricTypeForm'
import defaultImage from'./images/Default_Image_Thumbnail.png';
import { TextField } from "@material-ui/core";

import {
    Button,
    Paper,
    Grid,
    Table,
    TableBody,
    TableRow,
    TableCell,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    FormHelperText,
    CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var initialValues = {
    id: 0,
    name: "",
    fabric_reference: "",
    fabric_composition: "",
    fabric_construction: "",
    fabric_weight: "",
    fabric_cuttable_width: "",
    wash_type: "",
    designer: "",
    sample_type: "",
    buyer: "",
    sample_quantity: "",
    fob_price: "",
    fob_currency: "",
    created_at: new Date(),
    remark: "",
  };


const FabricIssueForm = (props) => {

    const { addOrEdit, recordForEdit, styleList } = props;

    const [washType, setWashType] = useState([]);
    const [designer, setDesigner] = useState([]);
    const [currency, setCurrency] = useState([]);
    const [sample_type, setSampleType] = useState([]);
    const [fabric, setFabric] = useState([]);
    const [fabricMillReference, setFabricMillReference] = useState([])
    const [fabricComposition, setFabricComposition] = useState();
    const [fabricConstruction , setFabricConstruction] = useState()
    const [fabricWeight, setFabricWeight] = useState();
    const [fabricCuttableWidth, setFabricCuttableWidth] = useState();
    const [displayFabricTable , setDisplayFabricTable] = useState(false)
    const [currentFile, setCurrentFile] = useState()
    const [previewImage, setPreviewImage] = useState(defaultImage)
    const [imageObject, setImageObject] = useState()
    

  const validationSchema = yup.object().shape({
    name: yup.string()
    .required("Name is required")
    .test("Unique", "Name needs te be unique", (values) => {
        if(styleList){
            return duplicateNameCheck(styleList, recordForEdit, values)
        }
        return true
    }),
    wash_type: yup.string().required("Wash Type is required"),
    designer: yup.string().required("Designer is required"),
    sample_type: yup.string().required("Sample Type is required"),
    // buyer: yup.string().required("Buyer is required"),
    // sample_quantity: yup.number()
    //     .typeError("Invalid Input: numbers please")
    //     .positive("Must be greater than zero")
    //     .required("Sample Quantity is required")
    //     .integer(),
    fob_price: yup.number()
        .typeError("Invalid Input: numbers please")
        .positive("Must be greater than zero")
        .required("Fob is required"),
    fob_currency: yup.string().required("Fob currency is required"),
  });

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      let data = new FormData();
    // Convert the original datetime string to a JavaScript Date object
    const originalDate = new Date(values.created_at);

    // Format the Date object to the desired format
    const formattedDatetimeString = originalDate.toISOString();
      data.append('id', values.id);
      data.append('name', values.name);
      data.append('fabric_reference', values.fabric_reference);
      data.append('fabric_composition', fabricComposition);
      data.append('fabric_construction', fabricConstruction);
      data.append('fabric_weight', fabricWeight);
      data.append('wash_type', values.wash_type);
      data.append('designer', values.designer);
      data.append('sample_type', values.sample_type);
      data.append('sample_quantity', values.sample_quantity);
      data.append('buyer', values.buyer);
      data.append('fob_price', values.fob_price);
      data.append('fob_currency', values.fob_currency);
      data.append('created_at', formattedDatetimeString);
      data.append('remark', values.remark);
      if(imageObject !=undefined){
          data.append('image', imageObject);
      }
        if(fabricComposition !=undefined){
            data.append('fabric_composition', fabricComposition);
        }
        if(fabricConstruction !=undefined){
            data.append('fabric_construction', fabricConstruction);
        }
        if(fabricWeight !=undefined){
            data.append('fabric_weight', fabricWeight);
        }
        if(fabricCuttableWidth !=undefined){
            data.append('fabric_cuttable_width', fabricCuttableWidth);
        }
      addOrEdit(data, resetForm, setSubmitting);
    },
  });

  let fabric_id = formik.values.fabric_reference

    const getFabricComposition = ()=>{
        fabric.map((item)=>{
            if(item.id == fabric_id){
                return setFabricComposition(item.fabric_composition)
            }
        })
    }

    const getFabricConstruction = ()=>{
        fabric.map((item)=>{
            if(item.id == fabric_id){
                return setFabricConstruction(item.fabric_construction)
            }
        })
    }

    const getFabricWeight = ()=>{
        fabric.map((item)=>{
            if(item.id == fabric_id){
                return setFabricWeight(item.weight_with_unit)
            }
        })
    }

    const getFabricCuttableWidth = ()=>{
        fabric.map((item)=>{
            if(item.id == fabric_id){
                return setFabricCuttableWidth(item.cuttable_width_with_unit)
            }
        })
    }

    function replaceNameWithMillReference(data) {
        return setFabricMillReference(data.map(item => {
            return {
                ...item,
                name: item.mill_reference
            };
        }));
    }

  useEffect(() => {
    async function getWashTypes() {
        const response = await fetch("/api/wash_type/list");
        const body = await response.json();
        setWashType(body);
    }
    getWashTypes();
    async function getDesigner() {
        const response = await fetch("/api/designer/list");
        const body = await response.json();
        setDesigner(body);
    }
    getDesigner();
    async function getProperty() {
        const response = await fetch("/api/sample_type/list");
        const body = await response.json();
        setSampleType(body);
    }
    getProperty();
    async function getCurrency() {
        const response = await fetch("/api/currency/list");
        const body = await response.json();
        setCurrency(body);
    }
    getCurrency();
    async function getFabric() {
        const response = await fetch("/api/fabric/style/list/");
        const body = await response.json();
        setFabric(body);
    }
    getFabric();

    }, []);

    useEffect(() => {
        replaceNameWithMillReference(fabric);
    }, [fabric])

  useEffect(() => {
    
    if (recordForEdit != null){
        if(recordForEdit.image){
            setPreviewImage(recordForEdit.image)
            setFabricComposition(recordForEdit.fabric_composition)
            setFabricConstruction(recordForEdit.fabric_construction)
            setFabricWeight(recordForEdit.fabric_weight)
            setFabricCuttableWidth(recordForEdit.fabric_cuttable_width)
        }
      formik.setValues({
        ...recordForEdit,
      });
    }

    getFabricComposition();
    getFabricConstruction();
    getFabricWeight();
    getFabricCuttableWidth();
        
  }, [recordForEdit, formik.values.fabric_reference]);


  return(
    <div>
        <Form onSubmit={formik.handleSubmit}>
            <Grid justify="space-between" container spacing={1} style={{ paddingLeft: 5, paddingRight: 5, paddingBottom: 5 }}>
                <Grid item md={9}>
                    <Paper elevation={1} square={false} style={{ padding: 5, paddingBottom: 15 }}>
                        <Grid container justify="space-between" alignItems="flex-start" spacing={1}>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Input
                                    label="Name"
                                    name="name"
                                    id="name"
                                    value={formik.values.name}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.name && Boolean(formik.errors.name)}
                                    helperText={formik.touched.name && formik.errors.name}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Select
                                    label="Sample Type"
                                    name="sample_type"
                                    value={formik.values.sample_type}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    options={sample_type}
                                    error={formik.touched.sample_type && Boolean(formik.errors.sample_type)}
                                    helperText={formik.touched.sample_type && formik.errors.sample_type}
                                    fullWidth   
                                />
                            </Grid>
                            {/* <Grid item md={4} sm={4} xs={12}>
                                <FormControl
                                    variant="outlined"
                                    fullWidth
                                    {...(formik.touched.fabric && {
                                    error: Boolean(formik.errors.fabric),
                                    })}
                                >
                                    <InputLabel>Fabric</InputLabel>
                                        <Select
                                            label="Fabric"
                                            name="fabric_reference"
                                            value={formik.values.fabric_reference}
                                            onChange={formik.handleChange}
                                            onBlur={formik.handleBlur}
                                            >
                                            <MenuItem value="">None</MenuItem>
                                            {fabric.map((item) => (
                                                <MenuItem key={item.id} value={item.id}>
                                                {item.dekko_reference}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    {formik.touched.fabric_reference &&
                                    Boolean(formik.errors.fabric_reference) && (
                                        <FormHelperText error={true}>
                                        Please select a fabric
                                        </FormHelperText>
                                    )}
                                </FormControl>
                            </Grid> */}

                            <Grid item md={3} sm={6} xs={6}>
                                <Autocomplete
                                    id="fabric_reference"
                                    options={fabric}
                                    hide = "true"
                                    renderInput={params => (
                                    <TextField {...params} label="Dekko Reference" variant="outlined"
                                        error={formik.touched.fabric_reference && Boolean(formik.errors.fabric_reference)}
                                        helperText={formik.touched.fabric_reference && formik.errors.fabric_reference}
                                    />
                                    )}
                                    getOptionLabel={option => option.name}
                                    getOptionSelected={(option, value) => option.id === value.id}
                                    value={fabric.find((type) => type.id === formik.values.fabric_reference) || formik.values.fabric_reference || null}
                                    onChange={(_event, fabric_reference) => {
                                        formik.setFieldValue("fabric_reference", fabric_reference ? fabric_reference.id : null);
                                    }}
                                    onBlur={formik.handleBlur}
                                />
                            </Grid>
                            <Grid item md={3} sm={6} xs={6}>
                                <Autocomplete
                                    id="fabric_reference"
                                    options={fabricMillReference}
                                    hide = "true"
                                    renderInput={params => (
                                        <TextField {...params} 
                                            label="Mill Reference" 
                                            variant="outlined"
                                            error={formik.touched.fabric_reference && Boolean(formik.errors.fabric_reference)}
                                            helperText={formik.touched.fabric_reference && formik.errors.fabric_reference}
                                        />
                                    )}
                                    getOptionLabel={option => option.name}
                                    getOptionSelected={(option, value) => option.id === value.id}
                                    value={fabric.find((type) => type.id === formik.values.fabric_reference) || formik.values.fabric_reference || null}
                                    onChange={(_event, fabric_reference) => {
                                        formik.setFieldValue("fabric_reference", fabric_reference ? fabric_reference.id : null);
                                    }}
                                    onBlur={formik.handleBlur}
                                />
                            </Grid>
                            <Grid item md={4} sm={4} xs={12}>
                                <Controls.Input
                                    name="fabric_composition"
                                    value = {fabric_id ? fabricComposition : ""}
                                    onChange={formik.handleChange}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Input
                                    name="fabric_construction"
                                    value={fabric_id ? fabricConstruction : ""}
                                    onChange={formik.handleChange}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Input
                                    name="fabric_weight"
                                    value={fabric_id ? fabricWeight : ""}
                                    onChange={formik.handleChange}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={2} sm={4} xs={12}>
                                <Controls.Input
                                    name="fabric_cuttable_width"
                                    value={fabric_id ? fabricCuttableWidth : ""}
                                    onChange={formik.handleChange}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                {/* <Controls.Select
                                    label="Wash Type"
                                    name="wash_type"
                                    value={formik.values.wash_type}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    options={washType}
                                    error={formik.touched.wash_type && Boolean(formik.errors.wash_type)}
                                    helperText={formik.touched.wash_type && formik.errors.wash_type}
                                    fullWidth   
                                /> */}
                                <Controls.Input
                                    label="Wash Type"
                                    name="wash_type"
                                    value={formik.values.wash_type}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.wash_type && Boolean(formik.errors.wash_type)}
                                    helperText={formik.touched.wash_type && formik.errors.wash_type}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Input
                                    label="Buyer"
                                    name="buyer"
                                    value={formik.values.buyer}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.buyer && Boolean(formik.errors.buyer)}
                                    helperText={formik.touched.buyer && formik.errors.buyer}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Select
                                    label="Designer"
                                    name="designer"
                                    value={formik.values.designer}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    options={designer}
                                    error={formik.touched.designer && Boolean(formik.errors.designer)}
                                    helperText={formik.touched.designer && formik.errors.designer}
                                    fullWidth   
                                />
                            </Grid>
                            
                            
                            <Grid item md={3} sm={4} xs={12}>
                                <Controls.Input
                                    label="Sample Quantity"
                                    name="sample_quantity"
                                    value={formik.values.sample_quantity}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.sample_quantity && Boolean(formik.errors.sample_quantity)}
                                    helperText={formik.touched.sample_quantity && formik.errors.sample_quantity}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={2} sm={3} xs={8}>
                                <Controls.Input
                                    label="FOB"
                                    name="fob_price"
                                    value={formik.values.fob_price}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    error={formik.touched.fob_price && Boolean(formik.errors.fob_price)}
                                    helperText={formik.touched.fob_price && formik.errors.fob_price}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={1} sm={1} xs={4}> 
                                <Controls.Select
                                    label=""
                                    name="fob_currency"
                                    value={formik.values.fob_currency}
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    options={currency}
                                    error={formik.touched.fob_currency && Boolean(formik.errors.fob_currency)}
                                    helperText={formik.touched.fob_currency && formik.errors.fob_currency}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={3} sm={4} xs={6}>
                                <Controls.DatePicker
                                    label="Date"
                                    name="created_at"
                                    value={formik.values.created_at}
                                    onChange={value => {
                                    formik.setFieldValue("created_at", value)
                                    }}
                                    onBlur={formik.handleBlur}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item md={6} sm={4} xs={12}>
                                    <Controls.Input
                                        label="Remark"
                                        name="remark"
                                        value={formik.values.remark}
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        fullWidth
                                        multiline
                                        rows={1}
                                    />
                            </Grid>
                                           
                            <Grid item md={12} sm={12} xs={12}>
                                <div className="mg20">
                                    <Grid container justify="space-between" spacing={2}>
                                        <Grid justify="flex-start" item md={12} sm={12} xs={12}>
                                            <label htmlFor="btn-upload">
                                                <input
                                                    id="btn-upload"
                                                    name="image"
                                                    style={{ display: 'none' }}
                                                    type="file"
                                                    accept="image/*"
                                                    onChange={(e)=>{
                                                        setCurrentFile(e.target.files[0])
                                                        setPreviewImage(URL.createObjectURL(e.target.files[0]))
                                                        setImageObject(e.target.files[0])
                                                    }}
                                                />
                                                <Grid  alignItems="center" container justify="space-between"spacing={2}>
                                                    <Grid item md={4} sm={4} xs={12}>
                                                        <Button
                                                            className="btn-choose"
                                                            variant="outlined"
                                                            component="span" >
                                                            Choose Image
                                                        </Button>
                                                    </Grid>
                                                    <Grid item md={2} sm={2} xs={12}>
                                                        <div className="file-name">
                                                            {currentFile ? currentFile.name : null}
                                                        </div>
                                                    </Grid>
                                                    <Grid item md={6} sm={6} xs={12}>
                                                    </Grid>
                                                </Grid>
                                            </label>
                                        </Grid>
                                        {/* Image */}
                                    </Grid>
                                    </div>
                                </Grid>
                                
                                <Grid item style={{ marginTop: 3 }} md={12} sm={12} xs={12}>
                                    <div className={classes.wrapper}>
                                        <Controls.Button
                                            type="submit"
                                            disabled={formik.isSubmitting}
                                            text="Submit"
                                        />                                  
                                        <Controls.Button
                                            text="Reset"
                                            color="default"
                                            onClick = {(e)=>{
                                                formik.resetForm(e)
                                                setDisplayFabricTable(false)
                                            }}
                                        />
                                    </div> 
                                </Grid>
                        </Grid>                          
                    </Paper>
                </Grid>
                <Grid item md={3}>
                    <Grid container alignItems="center" spacing={1}>
                        <Grid align='center' item md={12} sm={12} xs={12} >
                            {previewImage && (
                            <div>
                                <img className="preview my20" src={previewImage} alt="Style Image"  height="60%" width="60%" />                                                        </div>
                            )}
                        </Grid>
                    </Grid>
                </Grid>           
            </Grid>
        </Form>
    </div>
)
};

export default FabricIssueForm;
