import React, { useReducer } from "react";
import { FabricReducer } from "./reducer";
const FabricStateContext = React.createContext();
const FabricDispatchContext = React.createContext();

export function useFabricState() {
  const context = React.useContext(FabricStateContext);
  if (context === undefined) {
    throw new Error("useAuthState must be used within a AuthProvider");
  }

  return context;
}

export function useFabricDispatch() {
  const context = React.useContext(FabricDispatchContext);
  if (context === undefined) {
    throw new Error("useFabricDispatch must be used within a AuthProvider");
  }

  return context;
}

export const FabricProvider = ({ children, initialState }) => {
  const [fabric, dispatch] = useReducer(FabricReducer, initialState);

  return (
    <FabricStateContext.Provider value={fabric}>
      <FabricDispatchContext.Provider value={dispatch}>
        {children}
      </FabricDispatchContext.Provider>
    </FabricStateContext.Provider>
  );
}
