import React, { useState, useEffect } from "react";
import AddIcon from "@material-ui/icons/Add";
import BreadCrumb from "../../components/BreadCrumb/BreadCrumb";
import MUIDataTable from "mui-datatables";
import EditIcon from "@material-ui/icons/Edit";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";
import axios from "axios";
import { Link } from "react-router-dom";
import Upload from "../../components/Controls/Upload";
import IconButton from "@material-ui/core/IconButton";
import Popup from "../../components/Controls/Popup";
import Notification from "../../components/SnackBar/Notification";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles, Tooltip } from "@material-ui/core";
import StyleForm from "./StyleForm_1";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(2),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: "75%",
  },
  newButton: {
    position: "absolute",
    right: "10px",
  },
  iconButtonColor: {
    color: "#fff",
    '&:hover': {
      color: "#0000ff"
  }
  }
}));

export default function Suppliers() {
  const classes = useStyles();
  const [styleList, setStyleList] = useState([])
  const [recordForEdit, setRecordForEdit] = useState(null);
  const [searchValue, setSearchValue] = useState(null);
  const [searchOpen, setSearchOpen] = useState(false);
  const [records, setRecords] = useState(null);
  const [styleRecord, setStyleRecord] = useState(null);
  const [openPopup, setOpenPopup] = useState(false);

  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableToolbar: {
          root: {
            backgroundColor: "#50d07d",
            color: "#fff",
          },
          icon: {
            color: "#fff",
            "&:hover": {
              color: "#0000ff",
            },
          },
        },
        MuiTableCell: {
          root: {  //This can be referred from Material UI API documentation. 
              padding: '6px',
          },
        },
        MuiDialog:{
          paperWidthMd:{
            maxWidth: '1200px',
          }
         }
      },
    typography: {
      "fontFamily": `"Roboto", "Helvetica", "Arial", sans-serif`,
      "fontSize": 14,
      "fontWeightLight": 300,
      "fontWeightRegular": 400,
      "fontWeightMedium": 500
     }
    });

    async function fetchStyles() {
      const requestOptions = {
        headers: {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        },
      };
  
      try {
        await axios
          .get("/api/style/list", { requestOptions })
          .then((res) => {
              setStyleList(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      } catch (error) {
        console.log(error);
      }
    }

    const postStyle = async (values, setSubmitting) => {
      const requestOptions = {
        headers: {
          method: "POST",
          headers: {
            "Access-Control-Allow-Origin": "*",
            'content-type': 'multipart/form-data'
          },
        },
      };
  
      try {
          await axios
          .post("/api/style/create/", values, {
            requestOptions,
          })
          .then((resp) => {
            setStyleRecord(resp.data);
            setSubmitting(false);
          });
      } catch (error) {
        console.log(error);
      }
    };

    const updateStyle = async (values, id, setSubmitting) => {
      const requestOptions = {
        headers: {
          method: "PUT",
          headers: {
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "multipart/form-data",
          },
        },
      };
  
      try {
        await axios
          .put(`/api/style/update/${id}/`, values, {
            requestOptions,
          })
          .then((resp) => {
            setStyleRecord(resp.data);
            setSubmitting(false);
          });
      } catch (error) {
        console.log(error);
      }
    };

  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    fetchStyles();
  }, [styleRecord, searchOpen]);

  const openInPopup = (item) => {
    setRecordForEdit(item);
    setOpenPopup(true);
  };

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "wash_type",
      label: "Wash Type",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "designer_name",
      label: "Designer",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "fob",
      label: "FOB",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "buyer",
      label: "Buyer",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "sample_type_name",
      label: "Sample Type",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "sample_quantity",
      label: "Sample Quantity",
      options: {
        filter: true,
        sort: false,
      },
    },
    // {
    //   name: "code",
    //   label: "Barcode",
    //   options: {
    //     filter: false,
    //     sort: false,
    //     display: true,
    //     searchable: true
    //   },
    // },
    {
      name: "Actions",
      options: {
        filter: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          let item;
          tableMeta.tableData.forEach(function (style) {
            if (style.id == tableMeta.rowData[0]) return (item = style);
          });
          return (
            <>
              {/* <Link
                to={{
                  pathname: "/app/style/form",
                  state: {
                    item: item,
                    styleList: styleList
                  },
                }}
              >
                <IconButton color="primary" >
                  <EditIcon fontSize="medium" />
                </IconButton>
              </Link> */}

            <IconButton
                color="primary"
                onClick={() => {
                  openInPopup(item);
                }}
              >
                <EditIcon fontSize="default" />
              </IconButton>

              <Link
                to={{
                  pathname: "/app/style/details",
                  state: item
                }}
              >
                <IconButton color="primary">
                  <ArrowRightAltIcon fontSize="medium" />
                </IconButton>
              </Link>
            </>
          );
        },
      },
    },
  ];

  const addOrEdit = (style, resetForm, setSubmitting) => {
    for (var key of style.entries()) {
      console.log(key)
      if(key[0]=='id'){
          let id = key[1]
          if(key[1] == 0){
            postStyle(style, setSubmitting)
          }else{
            updateStyle(style, id, setSubmitting);
          }
      }
  }
    resetForm();
    setRecordForEdit(null);
    setOpenPopup(false);
    setNotify({
      isOpen: true,
      message: "Submitted Successfully",
      type: "success",
    });
  };

  const options = {
    filterType: "dropdown",
    selectableRows: "none",
    responsive: "standard",
    customToolbar: () => {
      return (
        <>
        <Tooltip title={"Add New"}>
          <IconButton className={classes.iconButtonColor}
            onClick={() => {
              setOpenPopup(true);
              setRecordForEdit(null);
            }}
          >
            <AddIcon />
          </IconButton>
        </Tooltip>
        </>
      );
    },
    print: false,
    download: false
  };

  return (
    <MuiThemeProvider theme={getMuiTheme()}>
      <div>
      <BreadCrumb routeSegments={[{ name: "Style" }]} />
        <MUIDataTable
          title={"Style List"}
          data={styleList}
          columns={columns}
          options={options}
          className={classes.pageContent}
        />
        <Popup
          title="Style Form"
          openPopup={openPopup}
          setOpenPopup={setOpenPopup}
        >
          <StyleForm
            recordForEdit={recordForEdit}
            addOrEdit={addOrEdit}
            styleList={styleList}
          />
        </Popup>
        <Notification notify={notify} setNotify={setNotify} />
      </div>
    </MuiThemeProvider>
  );
}
