import React, { useState, useEffect } from "react";
import AddIcon from "@material-ui/icons/Add";
import MUIDataTable from "mui-datatables";
import EditIcon from "@material-ui/icons/Edit";
import axios from "axios";
import IconButton from "@material-ui/core/IconButton";
import Paper from "@material-ui/core/Paper";
import Popup from "../../components/Controls/Popup";
import Notification from "../../components/SnackBar/Notification";
import { makeStyles, Tooltip } from "@material-ui/core";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import BreadCrumb from "../../components/BreadCrumb/BreadCrumb";
import FabricReportForm from "./FabricReportForm";
import { orange } from "@material-ui/core/colors";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(3),
  },
  iconButtonColor: {
    color: "#fff",
    "&:hover": {
      color: "#0000ff",
    },
  },
}));

export default function FabricReport() {
  const classes = useStyles();
  const [fabricSearchList, setFabricSearchList] = useState([]);
  const [recordForEdit, setRecordForEdit] = useState(null);

  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableToolbar: {
          root: {
            backgroundColor: "#50d07d",
            color: "#fff",
          },
          icon: {
            color: "#fff",
            "&:hover": {
              color: "#0000ff",
            },
          },
        },
        MUIDataTableHeadCell:{
          fixedHeader:{
              backgroundColor: "orange",
          }
        },
        MuiTableCell: {
          root: {
              padding: '5px',
          },
          head: {
              color: "Black",
          }
        },
      },
      typography: {
        "fontFamily": `"Roboto", "Helvetica", "Arial", sans-serif`,
        "fontSize": 10,
        "fontWeightLight": 300,
        "fontWeightRegular": 400,
        "fontWeightMedium": 500
       }
    });

  async function fetchFabricSearchList(values, setSubmitting) {
    const requestOptions = {
      headers: {
        method: "GET",
        headers: { "Content-Type": "application/json" },
      },
    };

    try {
      await axios
        .get("/api/fabric/search/", { params:  values  }, { requestOptions })
        .then((res) => {
          setFabricSearchList(res.data);
          setSubmitting(false);
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (error) {
      console.log(error);
    }
  }


  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    fetchFabricSearchList();
  }, []);

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "dekko_reference",
      label: "Dekko Reference",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "mill_reference",
      label: "Mill Reference",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "supplier_name",
      label: "Supplier",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "fabric_type_name",
      label: "Fabric Type",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "weave_name",
      label: "Weave",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "appearance_name",
      label: "Appearance",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "fabric_composition",
      label: "Composition",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "fabric_construction",
      label: "Construction",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "shrinkage",
      label: "Shrinkage",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "weight",
      label: "Weight",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "initial_availability",
      label: "Initial Availability",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "last_availability",
      label: "Last Availability",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "issue_date",
      label: "Issue Date",
      options: {
        filter: true,
        sort: true,
      },
    },
  ];

  const addOrEdit = (fiberPercentage, resetForm, setSubmitting) => {
    if (fiberPercentage.id === 0)
      fetchFabricSearchList(fiberPercentage, setSubmitting);
    resetForm();
    setRecordForEdit(null);
    setNotify({
      isOpen: true,
      message: "Submitted Successfully",
      type: "success",
    });
  };

  const options = {
    selectableRows: "none",
    search: false,
    filter:false,
    responsive: "standard",
    print: true,
    download: true,
    resizableColumns: true,
  };

  return (
    <MuiThemeProvider theme={getMuiTheme()}>
      <div>
        <BreadCrumb
          routeSegments={[
            { name: "Reports", path: "/app/fabric/report" },
            { name: "Fabric" },
          ]}
        />
        <Paper className={classes.pageContent}>
          <FabricReportForm
            recordForEdit={recordForEdit}
            addOrEdit={addOrEdit}
          />
        </Paper>
        <MUIDataTable
          title={"Fabric Report"}
          data={fabricSearchList}
          columns={columns}
          options={options}
          className={classes.pageContent}
        />
        <Notification notify={notify} setNotify={setNotify} />
      </div>
    </MuiThemeProvider>
  );
}
