import React, { useEffect, useState, useRef } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import BreadCrumb from "../../components/BreadCrumb/BreadCrumb";
import FabricMultipleQRcode from "../../components/Barcode/FabricMultipleQRcode";
import { useReactToPrint } from "react-to-print";



import {
    Button,
    Paper,
    Grid,
    Table,
    TableBody,
    TableRow,
    TableCell,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    FormHelperText,
    CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var initialValues = {
  fabric: [],
};


const FabricQRcodePrint = (props) => {
    const [fabricList, setFabricList] = useState([]);
    const { addOrEdit} = props;
    const [selectedFabrics, setSelectedFabrics] = useState([]);

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    onSubmit: (values, { setSubmitting, resetForm }) => {
    //   console.log(selectedFabrics)
      console.log(values)
      setSelectedFabrics(values)
      addOrEdit(values, resetForm, setSubmitting);
    },
  });

  useEffect(() => {
        async function getFabricList() {
            const response = await fetch("/api/fabric/qr/code/data/list/");
            const body = await response.json();
            setFabricList(body);
        }
        getFabricList();
    }, []);

    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current,
    });


  return(
    <div>
        <BreadCrumb routeSegments={[{ name: "Fabrics QR code print" }]} />
        <Form onSubmit={formik.handleSubmit}>
            <Grid justify="space-between" container spacing={5} style={{ paddingLeft: 16, paddingRight: 16, paddingBottom: 24 }}>
                <Grid item md={6}>
                    <Paper elevation={3} square={false} style={{ padding: 24, paddingBottom: 24 }}>
                        <Grid container justify="space-between" alignItems="flex-start" spacing={2}>
                            <Grid item md={9} sm={9} xs={12}>
                            <Autocomplete
                                    id="fabric"
                                    options={fabricList}
                                    renderInput={params => (
                                    <TextField {...params} label="Fabric" variant="outlined" />
                                    )}
                                    getOptionLabel={option => option.dekko_reference}
                                    onChange={(_event, fabric) => {
                                        // setSelectedFabrics(fabric);
                                        formik.setFieldValue("fabric", fabric)
                                    }}
                                    multiple
                                />
                            </Grid>
                            <Grid item style={{ marginTop: 3 }} md={3} sm={3} xs={12}>
                                <div >
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    onClick={handlePrint}
                                >
                                    Print Qrcodes
                                </Button>
                                </div> 
                            </Grid>
                        </Grid>                          
                    </Paper>
                </Grid>        
            </Grid>
        </Form>
        <div >
        <FabricMultipleQRcode fabrics={fabricList} ref={componentRef} />
      </div>
    </div>
)
};

export default FabricQRcodePrint;
