import axios from "axios";
const ROOT_URL = "http://127.0.0.1:8000/";

export async function fabricFList(dispatch) {
  const requestOptions = {
    headers: {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    },
  };

  try {
    dispatch({ type: "REQUEST_FABRIC_LIST" });
    await axios
      .get("/api/fabric/list")
      .then((res) => {
        dispatch({
          type: "FABRIC_LIST_SUCCESS",
          payload: res.data,
        });
      })
      .catch((err) => {
        dispatch({
          type: "FABRIC_LIST_ERROR",
          error: err,
        });
      });

    // if (data) {
    //   dispatch({ type: 'FABRIC_LIST_SUCCESS', payload: data });
    //   return data
    // }

    // dispatch({ type: 'FABRIC_LIST_ERROR', error: data.errors[0] });
    // return;
  } catch (error) {
    dispatch({ type: "FABRIC_LIST_ERROR", error: error });
  }
}
