import React, { useReducer } from "react";

 
export const initialState = {
  fabricList: [],
  loading: false,
  errorMessage: null
};
 
export const FabricReducer = (initialState, action) => {
  switch (action.type) {
    case "REQUEST_FABRIC_LIST":
      return {
        ...initialState,
        loading: true
      };
    case "FABRIC_LIST_SUCCESS":
      return {
        ...initialState,
        fabricList: action.payload,
        loading: false
      };
 
    case "FABRIC_LIST_ERROR":
      return {
        ...initialState,
        loading: false,
        errorMessage: action.error
      };
 
    default:
      throw new Error(`Unhandled action type: ${action.type}`);
  }
}