import React, { useState, useRef } from "react";
import BreadCrumb from "../../components/BreadCrumb/BreadCrumb";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import Popup from "../../components/Controls/Popup";
import FabricBarcode from "../../components/Barcode/FabricBarcode";
import { useReactToPrint } from "react-to-print";
import {
  Paper,
  Grid,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Box,
} from "@material-ui/core";
import ArrowBackRoundedIcon from "@material-ui/icons/ArrowBackRounded";
import { CenterFocusStrong } from "@material-ui/icons";

function FabricDetail(props) {
  let fabric = props.location.state;

  if(fabric.last_available_fabric === "None Meter" || fabric.last_available_fabric === "None Kg" || fabric.last_available_fabric === "None Gram" || fabric.last_available_fabric === "None Yard"){
    fabric.last_available_fabric = "Fabric not yet used."
  }

  let splitBySlash = fabric.fabric_composition_update.split("/")
  var arr1 = []
  for (var i=0; i< splitBySlash.length; i++){
    arr1.push(splitBySlash[i])
  }

  var splitByHi_pen = arr1.join(' ').split("-")
  var arr2 = []
  for (var i=0; i< splitByHi_pen.length; i++){
    arr2.push(splitByHi_pen[i])
  }
  var fabric_composition = arr2.join(' ')

  const [openPopup, setOpenPopup] = useState(false);
  const componentRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });
  return (
    <div>
      <BreadCrumb routeSegments={[{ name: "Fabric Details" }]} />
      <Paper
        elevation={3}
        square={false}
        style={{ paddingTop: 6, margin: "16px" }}
      >
        <div
          className="px-4"
          style={{
            marginBottom: "1.25rem",
            alignItems: "center",
            justifyContent: "space-between",
            display: "flex",
          }}
        >
          <Link to="/app/fabrics">
            <IconButton>
              <ArrowBackRoundedIcon />
            </IconButton>
          </Link>
          <div>
          <Box mx={1} display="inline-flex">
            <Button
              variant="contained"
              color="primary"
              className="mr-4 py-2"
              style={{marginBottom: "5px"}}
              onClick={() => {
                setOpenPopup(true);
              }}
            >
              Show Qrcode
            </Button>
            </Box>
            <Box mx={1} display="inline-flex">
              <Button
                variant="contained"
                color="secondary"
                onClick={handlePrint}
              >
                Print Qrcode
              </Button>
            </Box>
          </div>
        </div>
        <div>
          <Grid container alignItems="flex-start" spacing={6}>
            <Grid item md={6} sm={12} xs={12}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell>Dekko Reference:</TableCell>
                    <TableCell align="right">
                      {fabric.dekko_reference}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Mill Reference:</TableCell>
                    <TableCell align="right">{fabric.mill_reference}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Supplier Name:</TableCell>
                    <TableCell align="right">{fabric.supplier_name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Buyer:</TableCell>
                    <TableCell align="right">
                      {fabric.buyer_name}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Fabric Type:</TableCell>
                    <TableCell align="right">
                      {fabric.fabric_type_name}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Composition:</TableCell>
                    <TableCell align="right">
                      {fabric_composition}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Weave:</TableCell>
                    <TableCell align="right">{fabric.weave_name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Finishing:</TableCell>
                    <TableCell align="right">{fabric.finishing_name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Appearance:</TableCell>
                    <TableCell align="right">{fabric.appearance_name}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Construction:</TableCell>
                    <TableCell align="right">
                      {fabric.fabric_construction}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </Grid>
            <Grid item md={6} sm={12} xs={12}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell>Shrinkage:</TableCell>
                    <TableCell align="right">{`Warp: ${fabric.warp}, Weft: ${fabric.weft}`}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>weight:</TableCell>
                    <TableCell align="right">{fabric.weight_with_unit}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Cuttable Width:</TableCell>
                    <TableCell align="right">{fabric.cuttable_width} {fabric.cuttable_width_unit_value}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Price:</TableCell>
                    <TableCell align="right">{fabric.price_free_on_board}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>MOQ:</TableCell>
                    <TableCell align="right">{fabric.moq} {fabric.moq_unit_value}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Lead Time:</TableCell>
                    <TableCell align="right">{fabric.lead_time} {fabric.lead_time_unit_value}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Initial Availability:</TableCell>
                    <TableCell align="right">{fabric.initial_available_fabric}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Current Availability:</TableCell>
                    <TableCell align="right">{fabric.last_available_fabric}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Marketing Tools:</TableCell>
                    <TableCell align="right">{fabric.marketing_tools}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell>Remark:</TableCell>
                    <TableCell align="right">{fabric.remark}</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </Grid>
          </Grid>
        </div>
      </Paper>
      <Popup title="Qrcode" openPopup={openPopup} setOpenPopup={setOpenPopup}>
        <FabricBarcode fabricDetails={fabric} />
      </Popup>
      <div style={{ display: "none" }}>
        <FabricBarcode fabricDetails={fabric} ref={componentRef} />
      </div>
    </div>
  );
}

export default FabricDetail;
